@import url(~animate.css/animate.css);
@import url("https://fonts.googleapis.com/css?family=Ubuntu:700");
@font-face {
  font-family: CamingoCode-Reg;
  src: url("../assets/fonts/CamingoCode-Regular.ttf"); }

@font-face {
  font-family: CamingoCode-Bold;
  src: url("../assets/fonts/CamingoCode-Bold.ttf"); }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1.5rem;
  color: #DBDBDB; }

body,
html,
main {
  background-color: #201f1f;
  width: 100vw;
  overflow-x: hidden;
  font-family: CamingoCode-Reg;
  font-size: .94rem;
  scroll-behavior: smooth; }

h1 {
  color: #005bd1; }

h2 {
  color: #005bd1; }

h3 {
  color: #005bd1; }

h4 {
  color: #005bd1; }

h5 {
  color: #005bd1; }

h6 {
  color: #005bd1; }

h1 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 3.25rem;
  line-height: 3.4rem;
  text-align: center;
  margin-bottom: 2rem; }
  h1 * {
    color: #005bd1; }

h2 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 3rem;
  color: #005bd1;
  margin-bottom: 2rem;
  margin-left: 5rem; }

h3 {
  font-size: 1.5rem; }

p {
  margin-bottom: 1rem; }

.text-code,
.bold {
  font-family: CamingoCode-Bold;
  z-index: 2; }

.blue {
  color: #005bd1; }

.red {
  color: #ff6262; }

.green {
  color: #1ad129; }

code {
  font-family: CamingoCode-Reg; }

@keyframes arrowAnimation {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(5px); }
  100% {
    transform: translateY(0); } }

.container {
  padding: 3.25rem;
  width: 100%;
  min-height: 100vh; }

[class*="flex"] {
  display: flex; }

.flex-center {
  justify-content: center;
  align-items: center; }

.flex-around {
  justify-content: space-around;
  align-items: center; }

.flex-center-vertical {
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.content {
  padding: 2rem 1rem; }

.grid-container {
  display: grid; }

.home {
  width: 100%;
  height: 100vh;
  background-color: #201f1f;
  color: #005bd1;
  position: relative; }
  .home a {
    z-index: 1;
    position: absolute;
    bottom: 0; }
  .home .Typist {
    line-height: 3.5rem; }

.particles {
  width: 100%;
  height: 100%;
  position: absolute; }

#About_Me {
  position: relative; }
  #About_Me img {
    filter: hue-rotate(185deg); }
  #About_Me .content > * {
    width: 40%; }

#Services .content {
  perspective: 1000px;
  padding-top: 2rem;
  flex-wrap: wrap; }

.service-container {
  width: 25%;
  min-width: 300px;
  margin: 20px;
  position: relative;
  transform-style: preserve-3d;
  transition: 0.6s;
  border: 1px #005bd1 solid; }
  .service-container img {
    width: auto;
    height: 300px;
    margin-bottom: 3rem; }
  .service-container .front {
    height: 100%;
    z-index: 2;
    width: 100%;
    padding: 1rem;
    background-color: #201f1f;
    backface-visibility: hidden; }
  .service-container .back {
    position: absolute;
    text-align: center;
    transform: rotateY(180deg);
    height: 100%;
    width: 100%;
    padding: .75rem;
    overflow: auto;
    color: #DBDBDB;
    background-color: #201f1f;
    backface-visibility: hidden; }
    .service-container .back p {
      margin-bottom: 0; }
  .service-container:hover {
    transform: rotateY(180deg); }

#Code .grid-container {
  width: 100%;
  padding: 2rem;
  grid-template-columns: repeat(12, 1fr); }

#Code .list {
  grid-column: span 3; }
  #Code .list__section {
    margin-bottom: 2rem; }
    #Code .list__section__name {
      margin-bottom: .75rem;
      font-size: 1.25rem;
      color: #005bd1; }
    #Code .list__section__element.active {
      color: #005bd1; }
    #Code .list__section__element:hover {
      cursor: pointer; }
  #Code .list ul {
    margin-left: 2rem; }

#Code .code-item {
  grid-column: span 9;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, 100px);
  transition: opacity 0.2s;
  opacity: 0; }
  #Code .code-item.appear {
    opacity: 1;
    transition: opacity 0.2s; }
  #Code .code-item.hide {
    opacity: 0;
    transition: opacity 0.2s; }
  #Code .code-item__icon {
    grid-column: span 6;
    grid-row: span 4; }
    #Code .code-item__icon__level {
      height: auto;
      margin-bottom: 1rem; }
    #Code .code-item__icon__image {
      width: 90%;
      max-height: 80%; }
  #Code .code-item__description {
    grid-column: span 6;
    grid-row: span 4;
    padding: 2rem; }

#Contact {
  overflow-x: hidden; }
  #Contact .console {
    background-color: rgba(219, 219, 219, 0.1);
    padding: 1.8rem;
    width: 50%;
    margin-left: 2rem; }
    #Contact .console .input-group {
      margin-bottom: 2px;
      width: 100%; }
      #Contact .console .input-group * {
        vertical-align: top; }
    #Contact .console input,
    #Contact .console textarea {
      resize: none;
      width: 80%;
      font-family: CamingoCode-Reg;
      background-color: rgba(219, 219, 219, 0.1);
      border: 0;
      color: #dbdbdb;
      padding: 1px 5px; }
    #Contact .console textarea {
      height: 6rem; }

footer {
  background-color: rgba(219, 219, 219, 0.1);
  width: 100%;
  padding: 3rem;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 4rem 1rem; }
  footer * {
    color: #005bd1; }
  footer .brand-container,
  footer .navigation,
  footer .social {
    grid-column: span 4;
    font-size: 1.2rem; }
  footer .brand-container p {
    text-align: center; }
  footer .logo {
    margin: 0 auto 1rem;
    max-height: 200px; }
  footer .navigation__container {
    width: 100%;
    height: 100%;
    list-style: none; }
    footer .navigation__container__element {
      margin: 0 auto 1rem; }
      footer .navigation__container__element__link {
        color: #005bd1;
        text-decoration: none; }
        footer .navigation__container__element__link:hover {
          cursor: pointer; }
  footer .social__links {
    width: 100%; }
  footer .social h4 {
    margin-bottom: 2rem; }
  footer .social img {
    width: 70%;
    margin: 0 auto; }
  footer .credits {
    grid-column: span 12;
    text-align: center; }

.Typist .Cursor {
  display: inline-block; }
  .Typist .Cursor--blinking {
    opacity: 1;
    animation: blink 1s linear infinite; }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.arrowDownIcon svg,
.arrowUpIcon svg {
  height: 7rem; }

.pointIcon svg {
  height: 1.1rem;
  margin-bottom: 6.9rem; }

.arrowDownLink {
  position: absolute;
  bottom: 0; }
  .arrowDownLink .triangle {
    transition: all ease .5s; }
  .arrowDownLink:hover .triangle {
    animation: arrowAnimation .7s ease infinite; }

header {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 20; }
  header nav {
    position: absolute;
    right: 0;
    padding: 2rem 4rem; }
    header nav ul {
      list-style: none; }
    header nav .nav-link {
      color: #005bd1;
      display: block;
      margin: 5px;
      text-decoration: none;
      font: 1.25rem CamingoCode-Bold;
      transition: all .5s ease; }
    header nav [class*='bracket'] {
      transition: all ease .6s; }
    header nav:hover {
      cursor: pointer; }
      header nav:hover .point-1 {
        animation: bounce 1s ease 0ms infinite; }
      header nav:hover .point-2 {
        animation: bounce 1s ease 80ms infinite; }
      header nav:hover .point-3 {
        animation: bounce 1s ease 160ms infinite; }
      header nav:hover [class*='bracket'] {
        transition: all ease 1s; }
      header nav:hover .bracket-open {
        transform: translateX(-15px);
        transition: all ease .6s; }
      header nav:hover .bracket-close {
        transform: translateX(15px);
        transition: all ease .6s; }
    header nav .bracketIcon {
      height: 35px; }
    header nav .pointIcon {
      height: 12px;
      margin: 1px; }
    header nav.open .menuIcon {
      opacity: 0;
      width: 0;
      transition: all .1s ease; }
    header nav.open .menuArray {
      opacity: 1;
      width: 430px;
      transition: all 1s ease;
      overflow: hidden; }
    header nav.closed .nav-link {
      margin: 0;
      transition: all 1s ease; }
    header nav.closed .menuArray {
      width: 0;
      opacity: 0;
      transition: all .8s ease;
      overflow: hidden; }
    header nav.closed .menuIcon {
      opacity: 1;
      width: auto;
      transition: all .5s ease 1s; }

@media (max-width: 992px) {
  .hideOnTablet {
    display: none; }
  .content {
    flex-wrap: wrap; }
  #About_Me img {
    width: 60%;
    margin-bottom: 50px; }
  #About_Me .content > article {
    width: 100%; }
  #Code .code-item__icon, #Code .code-item__description {
    grid-column: span 12; }
  #Code .code-item__icon {
    grid-row: 1 / 3; }
  #Code .code-item__description {
    grid-row: 3 / 5; }
  #Contact .console {
    width: 100%; }
    #Contact .console input, #Contact .console textarea {
      width: 70%; } }

@media (max-width: 768px) {
  .hideOnSm {
    display: none; }
  body {
    font-size: .8rem; }
  h1 {
    font-size: 2.7rem; }
  h2 {
    font-size: 2rem;
    text-align: center;
    margin-left: 0; }
  h3 {
    font-size: 1.3rem; }
  header nav {
    padding: 3rem 2rem; }
  .service-container {
    min-width: 270px; }
    .service-container img {
      height: 270px; }
  #Code .code-item.grid-container {
    margin-left: 1rem; }
  #Code .grid-container {
    padding: 0; }
  #Code .code-item__icon {
    grid-row: 1 / 2; }
  #Code .code-item__description {
    grid-row: 2 / span 3; }
  #Contact .console input, #Contact .console textarea {
    width: 100%; }
  footer .brand-container,
  footer .social {
    grid-column: span 6; } }

@media (max-width: 576px) {
  header nav {
    padding: 2rem 1rem; }
  .container {
    padding: 1rem; }
  footer {
    padding: 1rem; }
    footer .logo {
      height: 100px; }
  header nav {
    max-width: 100vw; }
    header nav .nav-link {
      font-size: .98rem;
      margin: 2px; }
    header nav.open .menuArray {
      width: 100%; } }
